* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    width: 100%;
    font-size: 1.2rem;
    padding: .25rem;
}

.card-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 350px));
    grid-template-rows: 200px;
    grid-gap: 1rem;
    justify-content: center;
  }
  
  .card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    max-height: 300px;
    overflow: hidden;
  }
  
  .card p {
    padding: 5px;
    text-align: center;
  }
  
  .card img {
      height: 70%;
      width: 100%;
  }
  